@use '../../Variables.scss' as var;
@import '../../assets/styles/Utils.scss';
@import '../../assets/styles/Adaptation.scss';

.Home {
  @include flex;
  width: 100vw;
  height: 100vh;
  color: #fff;
  background: #000;
}

.HomeLogo {
  position: relative;
  z-index: 9;
  width: pw(471);
  text-align: center;
}

.HomeLogoText {
  font-size: pw(24);
  margin-top: 10px;
}

.HomeLogo img {
  width: 100%;
}

.Background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  padding: 60px;
}

.BackgroundVideo {
  object-fit: contain;
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  // mix-blend-mode: overlay;
}

.Background {
  padding: 0px;
}

@media (max-width: 768px) {
  .BackgroundVideo {
    object-fit: contain;
  }
}
