@use '../../Variables.scss' as var;
@import '../../assets/styles/Utils';
@import '../../assets/styles/Adaptation.scss';

.Main {
  width: 100vw;
  height: 100vh;
  background: #000;
}

.Content {
  width: 100vw;
  height: 100vh;
}

.Header {
  @include flex(space-between);

  width: 100vw;
  position: fixed;
  z-index: 11;
  left: 0;
  top: 0;
  padding: pw(20) pw(40);
}

.HeaderLogoImage {
  width: pw(30);
  margin-right: pw(7);
}

.HeaderLogoText {
  width: pw(148);
}

.HeaderMenu {
  @include flex(flex-end);
}

.HeaderMenuItem {
  font-weight: 400;
  font-size: 16px;
  color: #ffffff;
  opacity: 0.7;
  margin-left: pw(40);
  cursor: pointer;
}

/** menu **/
.MenuLabel {
  margin-bottom: pw(130);
  font-size: pw(16);
  color: #ffffff;
  opacity: 0.7;
}

.MenuWarp {
  position: fixed;
  z-index: 13;
  left: 0;
  top: 0;
  height: 100vh;
  padding: pw(60) pw(114);
  color: #fff;
}

.MenuItem {
  margin: pw(24) 0;
  font-size: pw(24);
  margin-left: pw(35);
}

.ChildMenuItem {
  margin: pw(24) 0;
  font-size: pw(18);
  margin-left: pw(35);
}

.MenuItem .active {
  color: #369feb !important;
}

.MenuMask {
  position: fixed;
  z-index: 9;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
}

@media (max-width: 768px) {
  .HeaderLogoImage {
    width: 35px;
    margin-right: 7px;
  }

  .HeaderLogoText {
    width: 108px;
  }

  .MenuLabel {
    font-size: 18px;
    margin-bottom: 60px;
  }

  .Header {
    padding: 10px 15px;
  }

  .MenuItem {
    font-size: 14px;
  }

  .ChildMenuItem {
    font-size: 12px;
  }
}
